"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import {
    ArrowLeft,
    Save,
    X,
    User,
    Phone,
    Mail,
    MapPin,
    Star,
    Upload,
    Image as ImageIcon,
    Loader2,
    Calendar,
    TrendingUp,
    Clock,
    AlertCircle,
    CheckCircle,
    Building,
} from "lucide-react";

interface CustomerFormData {
    fullName: string;
    shortName: string;
    address: string;
    phone: string;
    image: string;
    source: string;
    referrer: string;
    referrerPhone: string;
    serviceGroup: string;
    marketingClassification: string;
    potentialLevel: string;
    salesPerson: string;
    needsNote: string;
    isActive: boolean;
    lat: number;
    lng: number;
}

const CreateCustomer = () => {
    const router = useRouter();
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [success, setSuccess] = useState<string | null>(null);
    const [uploadingImage, setUploadingImage] = useState(false);
    const [salesPersons, setSalesPersons] = useState<
        Array<{ _id: string; fullName: string; position: string }>
    >([]);
    const [serviceGroups, setServiceGroups] = useState<
        Array<{ _id: string; name: string }>
    >([]);

    const [formData, setFormData] = useState<CustomerFormData>({
        fullName: "",
        shortName: "",
        address: "",
        phone: "",
        image: "",
        source: "",
        referrer: "",
        referrerPhone: "",
        serviceGroup: "",
        marketingClassification: "",
        potentialLevel: "Trung bình",
        salesPerson: "",
        needsNote: "",
        isActive: true,
        lat: 0,
        lng: 0,
    });

    // Fetch sales persons and service groups
    useEffect(() => {
        const fetchData = async () => {
            try {
                const token = localStorage.getItem("token");
                if (!token) {
                    router.push("/login");
                    return;
                }

                // Fetch sales persons
                const salesResponse = await fetch("/api/employees", {
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                });

                // Fetch service groups
                const serviceGroupsResponse = await fetch(
                    "/api/service-groups",
                    {
                        headers: {
                            Authorization: `Bearer ${token}`,
                        },
                    },
                );

                if (salesResponse.ok) {
                    const salesData = await salesResponse.json();
                    if (salesData.success) {
                        setSalesPersons(salesData.data);
                    }
                    console.log("data:", salesData.data);
                }

                if (serviceGroupsResponse.ok) {
                    const serviceGroupsData =
                        await serviceGroupsResponse.json();
                    setServiceGroups(serviceGroupsData.data || []);
                    // console.log("data:", serviceGroupsData.data);
                }
            } catch (err) {
                console.error("Error fetching data:", err);
            }
        };

        fetchData();
    }, [router]);

    // Handle image upload to Cloudinary
    const handleImageUpload = async (
        e: React.ChangeEvent<HTMLInputElement>,
    ) => {
        const file = e.target.files?.[0];
        if (!file) return;

        // Validate file type
        if (!file.type.startsWith("image/")) {
            setError("Vui lòng chọn file ảnh");
            return;
        }

        // Validate file size (max 5MB)
        if (file.size > 5 * 1024 * 1024) {
            setError("Kích thước ảnh không được vượt quá 5MB");
            return;
        }

        setUploadingImage(true);
        setError(null);

        try {
            const formData = new FormData();
            formData.append("file", file);
            formData.append("upload_preset", "ton-cloudinary");

            const response = await fetch(
                `https://api.cloudinary.com/v1_1/dprxrbz1z/image/upload`,
                {
                    method: "POST",
                    body: formData,
                },
            );

            if (!response.ok) {
                throw new Error("Upload ảnh thất bại");
            }

            const data = await response.json();

            // Update form with image URL
            setFormData((prev) => ({
                ...prev,
                image: data.secure_url,
            }));
        } catch (err) {
            console.error("Error uploading image:", err);
            setError("Không thể tải ảnh lên. Vui lòng thử lại.");
        } finally {
            setUploadingImage(false);
        }
    };

    // Handle form input changes
    const handleInputChange = (
        e: React.ChangeEvent<
            HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
        >,
    ) => {
        const { name, value, type } = e.target;

        if (type === "checkbox") {
            const checked = (e.target as HTMLInputElement).checked;
            setFormData((prev) => ({
                ...prev,
                [name]: checked,
            }));
        } else {
            setFormData((prev) => ({
                ...prev,
                [name]: value,
            }));
        }
    };

    // Validate form
    const validateForm = () => {
        if (!formData.fullName.trim()) {
            setError("Vui lòng nhập tên đầy đủ của khách hàng");
            return false;
        }

        if (formData.phone && !/^[0-9+\-\s()]+$/.test(formData.phone)) {
            setError("Số điện thoại không hợp lệ");
            return false;
        }

        if (
            formData.referrerPhone &&
            !/^[0-9+\-\s()]+$/.test(formData.referrerPhone)
        ) {
            setError("Số điện thoại người giới thiệu không hợp lệ");
            return false;
        }

        return true;
    };

    // Handle form submission
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        if (!validateForm()) {
            return;
        }

        setLoading(true);
        setError(null);
        setSuccess(null);

        try {
            const token = localStorage.getItem("token");
            if (!token) {
                router.push("/login");
                return;
            }

            // Clean form data
            const submitData = {
                ...formData,
                shortName: formData.shortName.trim() || undefined,
                address: formData.address.trim() || undefined,
                phone: formData.phone.trim() || undefined,
                image: formData.image.trim() || undefined,
                source: formData.source.trim() || undefined,
                referrer: formData.referrer.trim() || undefined,
                referrerPhone: formData.referrerPhone.trim() || undefined,
                serviceGroup: formData.serviceGroup.trim() || undefined,
                marketingClassification:
                    formData.marketingClassification.trim() || undefined,
                salesPerson: formData.salesPerson.trim() || undefined,
                needsNote: formData.needsNote.trim() || undefined,
            };

            const response = await fetch("/api/customers", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
                body: JSON.stringify(submitData),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(
                    errorData.message || "Không thể tạo khách hàng mới",
                );
            }

            const data = await response.json();
            if (data.success) {
                setSuccess("Tạo khách hàng mới thành công!");
                setTimeout(() => {
                    router.push("/khach-hang");
                }, 2000);
            }
        } catch (err) {
            console.error("Error creating customer:", err);
            setError(err instanceof Error ? err.message : "Có lỗi xảy ra");
        } finally {
            setLoading(false);
        }
    };

    // Get potential level icon
    const getPotentialIcon = (level: string) => {
        switch (level) {
            case "Cao":
                return <TrendingUp className="w-4 h-4" />;
            case "Trung bình":
                return <Star className="w-4 h-4" />;
            case "Thấp":
                return <Clock className="w-4 h-4" />;
            default:
                return <Star className="w-4 h-4" />;
        }
    };

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            {/* Header */}
            <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
                <div className="flex items-center justify-between">
                    <div className="flex items-center">
                        <button
                            onClick={() => router.back()}
                            className="mr-4 p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition"
                        >
                            <ArrowLeft className="w-5 h-5" />
                        </button>
                        <div className="flex items-center">
                            <User className="w-8 h-8 text-blue-600 mr-3" />
                            <div>
                                <h1 className="text-2xl font-bold text-gray-900">
                                    Tạo khách hàng mới
                                </h1>
                                <p className="text-gray-600">
                                    Thêm thông tin khách hàng tiềm năng vào hệ
                                    thống
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Success/Error Messages */}
            {success && (
                <div className="mb-6 bg-green-50 border border-green-200 rounded-lg p-4">
                    <div className="flex items-center">
                        <CheckCircle className="w-5 h-5 text-green-600 mr-2" />
                        <p className="text-green-800">{success}</p>
                    </div>
                </div>
            )}

            {error && (
                <div className="mb-6 bg-red-50 border border-red-200 rounded-lg p-4">
                    <div className="flex items-center">
                        <AlertCircle className="w-5 h-5 text-red-600 mr-2" />
                        <p className="text-red-800">{error}</p>
                    </div>
                </div>
            )}

            {/* Form */}
            <form onSubmit={handleSubmit} className="space-y-6">
                {/* Basic Information */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                        <Building className="w-5 h-5 mr-2 text-blue-600" />
                        Thông tin cơ bản
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Tên đầy đủ{" "}
                                <span className="text-red-600">*</span>
                            </label>
                            <input
                                type="text"
                                name="fullName"
                                value={formData.fullName}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Nhập tên đầy đủ của khách hàng"
                                required
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Tên viết tắt
                            </label>
                            <input
                                type="text"
                                name="shortName"
                                value={formData.shortName}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Tên viết tắt (không bắt buộc)"
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Địa chỉ
                            </label>
                            <input
                                type="text"
                                name="address"
                                value={formData.address}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Địa chỉ khách hàng"
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Trạng thái
                            </label>
                            <div className="flex items-center">
                                <input
                                    type="checkbox"
                                    name="isActive"
                                    id="isActive"
                                    checked={formData.isActive}
                                    onChange={handleInputChange}
                                    className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                                />
                                <label
                                    htmlFor="isActive"
                                    className="ml-2 text-sm text-gray-700"
                                >
                                    Khách hàng hoạt động
                                </label>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Contact Information */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                        <Phone className="w-5 h-5 mr-2 text-blue-600" />
                        Thông tin liên hệ
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Số điện thoại
                            </label>
                            <input
                                type="tel"
                                name="phone"
                                value={formData.phone}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Số điện thoại liên hệ"
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Hình ảnh
                            </label>
                            <div className="space-y-3">
                                {/* Image Upload Area */}
                                <div className="relative">
                                    <input
                                        type="file"
                                        id="image-upload"
                                        accept="image/*"
                                        onChange={handleImageUpload}
                                        className="hidden"
                                        disabled={uploadingImage}
                                    />
                                    <label
                                        htmlFor="image-upload"
                                        className={`
                                            flex items-center justify-center w-full h-32 border-2 border-dashed rounded-lg cursor-pointer transition
                                            ${
                                                uploadingImage
                                                    ? "border-gray-300 bg-gray-50 cursor-not-allowed"
                                                    : "border-gray-300 hover:border-blue-400 hover:bg-blue-50"
                                            }
                                        `}
                                    >
                                        {uploadingImage ? (
                                            <div className="flex flex-col items-center">
                                                <Loader2 className="w-8 h-8 text-blue-600 animate-spin mb-2" />
                                                <span className="text-sm text-gray-600">
                                                    Đang tải ảnh...
                                                </span>
                                            </div>
                                        ) : formData.image ? (
                                            <div className="flex flex-col items-center">
                                                <ImageIcon className="w-8 h-8 text-green-600 mb-2" />
                                                <span className="text-sm text-green-600">
                                                    Đã tải ảnh
                                                </span>
                                                <span className="text-xs text-gray-500 mt-1">
                                                    Click để thay đổi
                                                </span>
                                            </div>
                                        ) : (
                                            <div className="flex flex-col items-center">
                                                <Upload className="w-8 h-8 text-gray-400 mb-2" />
                                                <span className="text-sm text-gray-600">
                                                    Tải ảnh lên
                                                </span>
                                                <span className="text-xs text-gray-500 mt-1">
                                                    JPG, PNG, GIF (max 5MB)
                                                </span>
                                            </div>
                                        )}
                                    </label>
                                </div>

                                {/* Image Preview */}
                                {formData.image && (
                                    <div className="relative">
                                        <img
                                            src={formData.image}
                                            alt="Customer preview"
                                            className="w-full h-48 object-cover rounded-lg border border-gray-200"
                                        />
                                        <button
                                            type="button"
                                            onClick={() =>
                                                setFormData((prev) => ({
                                                    ...prev,
                                                    image: "",
                                                }))
                                            }
                                            className="absolute top-2 right-2 bg-red-500 text-white p-1 rounded-full hover:bg-red-600 transition"
                                        >
                                            <X className="w-4 h-4" />
                                        </button>
                                    </div>
                                )}

                                {/* Hidden input for form submission */}
                                <input
                                    type="hidden"
                                    name="image"
                                    value={formData.image}
                                />
                            </div>
                        </div>
                    </div>
                </div>

                {/* Source and Classification */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                        <Star className="w-5 h-5 mr-2 text-blue-600" />
                        Nguồn và Phân loại
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Nguồn khách hàng
                            </label>
                            <select
                                name="source"
                                value={formData.source}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">-- Chọn nguồn --</option>
                                <option value="Google Ads">Google Ads</option>
                                <option value="Facebook">Facebook</option>
                                <option value="Sales tự tìm">
                                    Sales tự tìm
                                </option>
                                <option value="BGĐ giao">BGĐ giao</option>
                                <option value="CTV/ Referrals">
                                    CTV/ Referrals
                                </option>
                            </select>
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Mức độ tiềm năng
                            </label>
                            <select
                                name="potentialLevel"
                                value={formData.potentialLevel}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="Cao">Cao</option>
                                <option value="Trung bình">Trung bình</option>
                                <option value="Thấp">Thấp</option>
                            </select>
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Nhóm dịch vụ
                            </label>
                            <select
                                name="serviceGroup"
                                value={formData.serviceGroup}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">Chọn nhóm dịch vụ</option>
                                {serviceGroups.map((group) => (
                                    <option key={group._id} value={group.name}>
                                        {group.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        {/* <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Phân loại marketing
                            </label>
                            <input
                                type="text"
                                name="marketingClassification"
                                value={formData.marketingClassification}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Phân loại marketing"
                            />
                        </div> */}
                    </div>
                </div>

                {/* Referrer Information */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                        <User className="w-5 h-5 mr-2 text-blue-600" />
                        Thông tin người giới thiệu
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Tên người giới thiệu
                            </label>
                            <input
                                type="text"
                                name="referrer"
                                value={formData.referrer}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Tên người giới thiệu"
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Số điện thoại người giới thiệu
                            </label>
                            <input
                                type="tel"
                                name="referrerPhone"
                                value={formData.referrerPhone}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Số điện thoại người giới thiệu"
                            />
                        </div>
                    </div>
                </div>

                {/* Assignment and Notes */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                        <Calendar className="w-5 h-5 mr-2 text-blue-600" />
                        Phân công và Ghi chú
                    </h2>

                    <div className="grid grid-cols-1 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Nhân viên phụ trách
                            </label>
                            <select
                                name="salesPerson"
                                value={formData.salesPerson}
                                onChange={handleInputChange}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">-- Chọn nhân viên --</option>
                                {salesPersons.map((person) => (
                                    <option
                                        key={person._id}
                                        value={person.fullName}
                                    >
                                        {person.fullName} -{" "}
                                        {person.position || "Nhân viên"}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Nhu cầu và ghi chú
                            </label>
                            <textarea
                                name="needsNote"
                                value={formData.needsNote}
                                onChange={handleInputChange}
                                rows={4}
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                placeholder="Ghi chú về nhu cầu, yêu cầu của khách hàng..."
                            />
                        </div>
                    </div>
                </div>

                {/* Action Buttons */}
                <div className="bg-white rounded-lg shadow-sm p-6">
                    <div className="flex items-center justify-end space-x-4">
                        <button
                            type="button"
                            onClick={() => router.back()}
                            className="flex items-center px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition"
                        >
                            <X className="w-5 h-5 mr-2" />
                            Hủy
                        </button>
                        <button
                            type="submit"
                            disabled={loading}
                            className="flex items-center px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition disabled:opacity-50 disabled:cursor-not-allowed"
                        >
                            <Save className="w-5 h-5 mr-2" />
                            {loading ? "Đang lưu..." : "Lưu khách hàng"}
                        </button>
                    </div>
                </div>
            </form>
        </div>
    );
};

export default CreateCustomer;
